<template>
<div class="my-container">
    <div class="my-title">
        <i-row>
            <i-col span="6">
                <i-input v-model="searchWord" @on-search="searchInvlidItem" search placeholder="请输入关键词进行搜索" />
            </i-col>
            <i-col span="6">
                <i-datePicker type="datetimerange" placeholder="选择时间区间" format="yyyy/MM/dd HH:mm:ss" :transfer="true" @on-change="TimeChange"></i-datePicker>
            </i-col>
            <i-col span="3" offset="6">
                <i-button type="primary" icon="md-add" @click="newItemModalShow = true">添加</i-button>
            </i-col>
            <i-col span="3">
                <i-upload class="Url-batchUpload-button" ref="upload" action :show-upload-list="true" :before-upload="handleBeforeUpload">
                    <i-button class="Url-part114 Url-newItems-button" type="primary" icon="md-cloud-upload" @click="addInvalidDictItems">批量添加</i-button>
                </i-upload>
                <div class="Url-batchUpload-template">
                    <a :href=" baseurl + 'static/IgnoreWord-batchUpload-example.csv'" title="下载上传模板">下载添加模板</a>
                </div>
            </i-col>
        </i-row>

        <i-row>
            <i-col span="2">
                <i-button type="primary" icon="ios-refresh" @click="convertToStop">设为停止词</i-button>
            </i-col>
            <i-col span="2">
                <i-button type="primary" icon="ios-refresh" @click="convertToInvalid">设为无效词</i-button>
            </i-col>
            <i-col span="2">
                <i-button type="primary" @click="exportData(1)" icon="ios-download">导出数据</i-button>
            </i-col>
        </i-row>
    </div>
    <div>
        <div class="Url-part12"></div>
        <i-modal v-model="newItemModalShow" title="用户词" @on-ok="addItem" @on-cancel="cancelAddItem">
            <i-input v-model="currentWord" type="textarea" :rows="4" placeholder="请输入用户词..." />
            <label class="Url-itemPage-form-category-label">分类(可多选)</label>
            <i-select class="Url-itemPage-form-category-select" multiple v-model="categoriesData">
                <i-option v-for="item in categories" :value="item" :key="item">{{ item }}</i-option>
            </i-select>
        </i-modal>
        <i-modal v-model="modifyItemModalShow" title="无效词" @on-ok="modifyItem" @on-cancel="cancelAddItem">
            <i-input v-model="currentWord" type="textarea" :rows="4" placeholder="请输入无效词..." />
            <label class="Url-itemPage-form-category-label">分类(可多选)</label>
            <i-select class="Url-itemPage-form-category-select" multiple v-model="categoriesData">
                <i-option v-for="item in categories" :value="item" :key="item">{{ item }}</i-option>
            </i-select>
            <div slot="footer">
                <i-button type="error" size="large" @click="deleteItem">删除</i-button>
                <i-button type="primary" size="large" @click="modifyItem">提交</i-button>
            </div>
        </i-modal>
    </div>
    <i-table class="Url-table" :columns="columns1" :data="UserDictItemData" :loading="loading" @on-filter-change="handleFilter" @on-selection-change="handleSelectRow()" ref="table" stripe border>
        <template slot-scope="{ row,index }" slot="action">
            <div class="Url-actions">
                <i-button type="success" size="small" style="margin-right: 5px" @click="editInvalidDictItem(row,index)">修改</i-button>
            </div>
        </template>
    </i-table>
    <i-page :total="itemCount" :current="currentPage" :page-size="pageSize" :page-size-opts="[10,20,30,40,50,100]" size="small" show-elevator show-total show-sizer @on-change="pageChange" @on-page-size-change="pageSizeChange" />
</div>
</template>

<script>
import {
    mapState
} from "vuex";
export default {
    name: 'userDict',
    data() {
        return {
            newItemModalShow: false,
            modifyItemModalShow: false,
            currentWord: "",
            currentItem: undefined,
            searchWord: '',
            userList: [],
            dataRange: ['', ''],
            operatorChecked: '',
            categories: [],
            categoriesData: [],
            wordLengthChecked: [],
            categoryChecked: [],
            categoriesFilter: [],
            // ------------------------------------------------
            searchReaultListvisible: true,
            searchItem: "",
            website: "https://www.stockhey.com",
            detailIndex: 1,
            searchResult: [],
            selectedItemList: [],
            loading: false,
            itemCount: 0,
            currentPage: 1,
            pageSize: 10,
            select3: "Url",
            urlItemPageShow: false,
            urlItemPageTitle: "单条添加",
            columns1: [{
                    type: "selection",
                    align: "center",
                    width: 100,
                    resizable: true,
                    fixed: "left",
                },
                {
                    title: "用户词",
                    key: "word",
                    align: "center",
                    sortable: false,
                    resizable: true,
                },
                {
                    title: '分类',
                    key: 'categories',
                    align: 'left',
                    filters: [],
                    filterMultiple: true,
                    filterMethod(value, row) {
                        // 现在的筛选策略: 只要是其中 包含 选择项的， 就符合要求
                        //if (row.category.includes(value)){
                        return row //什么都不做，由专门的函数进行后端筛选
                        //}
                    },
                    render: (h, params) => {
                        // console.log(params.row)
                        try {
                            return (h('p', params.row.categories.join(';')))
                        } catch {
                            return ''
                        }

                    },
                    resizable: true,
                }, {
                    title: "时间",
                    key: "modifiedTime",
                    align: "left",
                    resizable: true,
                    renderHeader: function (h) {
                        //console.log(params)
                        return h('strong', '时间')
                    }
                },
                {
                    title: "操作人",
                    key: "operator",
                    align: "left",
                    filters: [],
                    filterMethod(value) {
                        // 现在的筛选策略: 只要是其中 包含 选择项的， 就符合要求
                        //if (row.status === value){
                        return value //什么都不做，由专门的函数进行后端筛选
                        //}
                    },
                    resizable: true,
                },
                {
                    title: "长度",
                    key: "length",
                    align: "center",
                    sortable: true,
                    resizable: true,
                    filters: [{
                        'label': '0-2',
                        'value': '1'
                    }, {
                        'label': '2-4',
                        'value': '2'
                    }, {
                        'label': '4-8',
                        'value': '3'
                    }, {
                        'label': '8以上',
                        'value': '4'
                    }],
                    filterMethod(value) {
                        // 现在的筛选策略: 只要是其中 包含 选择项的， 就符合要求
                        //if (row.status === value){
                        return value //什么都不做，由专门的函数进行后端筛选
                        //}
                    },
                },
                {
                    title: "操作",
                    key: "action",
                    width: 100,
                    slot: "action",
                    align: "center",
                    resizable: true,
                },
            ],
            UserDictItemData: [],
            formCustom: {},
        };
    },
    computed: {
        ...mapState([
            "baseurl",
            "currentComponent"
        ]),
    },
    mounted() {
        this.getUserInformation()
        this.fetchProjectCategories()
    },
    created() {
        this.fetchAllItems(); // 获取当前
    },
    components: {},
    methods: {
        addItem: function () {
            let operator = localStorage.getItem('kwmUser')
            let self = this;
            if (self.currentWord == '') {
                self.$Message.info("用户词不可为空!");
            } else {
                let allData = [{
                    word: self.currentWord,
                    categories: self.categoriesData,
                    operator: operator,
                    // xfrom: localStorage.getItem('kwmdepart')
                }, ];
                self
                    .axios({
                        method: "post",
                        url: self.baseurl + "UserDict/" + self.currentComponent,
                        data: allData,
                    })
                    .then((res) => {
                        if (res.data.count !== "") {
                            self.itemCount = res.data.count;
                        }
                        self.UserDictItemData = res.data.content;
                        self.currentWord = "";
                        self.$Message.success("新增成功!");
                    })
                    .catch((err) => {
                        console.log(err);
                        // self.$Message.error("新增失败!");
                        self.$Message.error({
                            content: JSON.stringify(err.response.data.detail),
                            duration: 0,
                            closable: true
                        });
                    });
            }
        },
        modifyItem: function () {
            let operator = localStorage.getItem('kwmUser')
            let self = this;
            if (self.currentWord == '') {
                self.$Message.info("用户词不可为空!");
            } else {
                let currentModify = {
                    word: self.currentWord,
                    categories: self.categoriesData,
                    operator: operator,
                    //xfrom: localStorage.getItem('kwmdepart')
                }
                let allData = currentModify
                self
                    .axios({
                        method: "put",
                        url: self.baseurl +
                            "UserDict/" +
                            self.currentComponent +
                            "/" +
                            self.currentModify._id.$oid,
                        data: allData,
                    })
                    .then((res) => {

                        if (res.data.count !== "") {
                            self.itemCount = res.data.count;
                        }
                        self.UserDictItemData = res.data.content;
                        self.currentWord = "";
                        self.$Message.success("修改成功!");

                    })
                    .catch((err) => {
                        console.log(err);
                        // self.$Message.error("修改失败!");
                        self.$Message.error({
                            content: JSON.stringify(err.response.data.detail),
                            duration: 0,
                            closable: true
                        });
                    });
                this.modifyItemModalShow = false
            }
        },
        cancelAddItem: function () {
            self.modal1 = false;
        },
        editInvalidDictItem: function (row) {
            this.currentWord = row.word;
            this.currentModify = row;
            this.modifyItemModalShow = true;
            this.categoriesData = row.categories;
        },
        convertToStop: function () {
            let self = this;
            // 目前只需要把 每一行对应的uid取出来，发送到后端进行删除就好了
            // console.log(this.selectedItemList)

            // 当没有选项被选中时，不进行该操作
            if (self.selectedItemList.length === 0) {
                //console.log('no delete')
                self.$Message.info("请选择数据");
            } else {
                let uids = [];
                for (let element in self.selectedItemList) {
                    uids.push(self.selectedItemList[element]);
                }
                console.log(uids)
                self
                    .axios({
                        method: "delete",
                        url: self.baseurl + "UserDict/" + self.currentComponent + '/toStop',
                        data: uids,
                    })
                    .then((res) => {
                        // console.log(res)
                        self.currentPage = 1;
                        if (res.data.count !== "") {
                            self.itemCount = res.data.count;
                        }
                        self.UserDictItemData = res.data.content;
                        self.$Message.success("操作成功!");
                    })
                    .catch(() => {
                        self.$Message.error("操作失败");
                    });
            }
        },
        convertToInvalid: function () {
            let self = this;
            // 目前只需要把 每一行对应的uid取出来，发送到后端进行删除就好了
            // console.log(this.selectedItemList)

            // 当没有选项被选中时，不进行该操作
            if (self.selectedItemList.length === 0) {
                //console.log('no delete')
                self.$Message.info("请选择数据");
            } else {
                let uids = [];
                for (let element in self.selectedItemList) {
                    uids.push(self.selectedItemList[element]);
                }
                // console.log(uids)
                self
                    .axios({
                        method: "delete",
                        url: self.baseurl + "UserDict/" + self.currentComponent + '/toInvalid',
                        data: uids,
                    })
                    .then((res) => {
                        // console.log(res)
                        self.currentPage = 1;
                        if (res.data.count !== "") {
                            self.itemCount = res.data.count;
                        }
                        self.UserDictItemData = res.data.content;
                        self.$Message.success("操作成功!");
                    })
                    .catch(() => {
                        self.$Message.error("操作失败,请检查无效词是否重复");
                    });
            }
        },
        exportData: function (type) {
            if (type === 1) {
                this.$refs.table.exportCsv({
                    filename: this.currentComponent,
                    columns: this.columns1.filter((col, index) => {
                        if (index > 0) {
                            return col;
                        }
                    }),
                    data: this.UserDictItemData.map((data) => {
                        const target = Object.assign({}, data);
                        // console.log(target)
                        console.log(target)
                        for (let x in target) {
                            target[x] = JSON.stringify(target[x]);
                            target[x] = target[x].replace(/,/g, ";");
                            // target[x] = target[x].replace(/"/g, "'");
                        }
                        return target;
                    }),
                });
            }
        },
        addInvalidDictItems: function () {},
        searchInvlidItem: function () {
            let self = this
            let data = {
                keyword: self.searchWord
            }
            self
                .axios({
                    method: "get",
                    url: self.baseurl + "UserDict/" + self.currentComponent + "?keyword=" + self.searchWord,
                    data: data,
                })
                .then((res) => {
                    // console.log(res)
                    self.currentPage = 1;
                    if (res.data.count !== "") {
                        self.itemCount = res.data.count;
                    }
                    console.log(res.data.content)
                    self.UserDictItemData = res.data.content;
                })
                .catch((err) => {
                    console.log(err)
                    self.$Message.error("搜索失败");
                });
        },
        handleFilter: function (column) {
            let self = this
            let chekced = {
                'key': column['key'],
                'checked': column['_filterChecked']
            }
            if (chekced.key == 'length') {
                self.wordLengthChecked = chekced['checked']
            } else if (chekced.key == 'operator') {
                self.operatorChecked = chekced['checked']
            } else if (chekced.key == 'categories') {
                self.categoryChecked = chekced['checked']
            }
            self.currentPage = 1
            let Params = {
                'currentPage': self.currentPage,
                'pageSize': self.pageSize,
                'dataRange': encodeURIComponent(self.dataRange),
                'urlPart': self.searchItem.toLowerCase(),
                'operatorFilter': encodeURIComponent(self.operatorChecked),
                'wordLengthFilter': encodeURIComponent(self.wordLengthChecked),
                'categoryFilter': encodeURIComponent(self.categoryChecked),
            }
            self.fetchAllItems(Params)
        },
        getUserInformation: function () {
            let self = this
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'Account/AllUsers',
                })
                .then(res => {
                    for (let ele in res.data.content) {
                        self.userList.push({
                            'label': res.data.content[ele].account,
                            'value': res.data.content[ele].account
                        })
                    }
                    // console.log('projetcCategoriesList',self.projetcCategoriesList)
                    self.columns1[5].filters = self.userList // 必须要设置一下 ，目录列表
                })
                .catch(err => {
                    console.log(err)
                })
        },
        TimeChange: function (daterange) {
            let self = this
            self.dataRange = daterange

            //日期被重新筛选，激发重新搜索
            let Params = {
                'currentPage': self.currentPage,
                'pageSize': self.pageSize,
                'dataRange': encodeURIComponent(self.dataRange),
                'operatorFilter': encodeURIComponent(self.operatorChecked),
                'wordLengthFilter': encodeURIComponent(self.wordLengthChecked)
            }
            self.fetchAllItems(Params)
        },
        deleteItem: function () {
            let self = this
            let items = [];
            items.push({
                word: self.currentModify["word"]
            });
            // console.log(uids)
            self
                .axios({
                    method: "delete",
                    url: self.baseurl + "UserDict/" + self.currentComponent,
                    data: items,
                })
                .then((res) => {
                    // console.log(res)
                    self.currentPage = 1;
                    if (res.data.count !== "") {
                        self.itemCount = res.data.count;
                    }
                    self.UserDictItemData = res.data.content;
                    self.$Message.success("删除成功!");
                })
                .catch((err) => {
                    console.log(err);
                    self.$Message.error("删除失败");
                });
            this.modifyItemModalShow = false
        },
        fetchProjectCategories: function () {
            // 获取所有 该项目下的所有 目录列表
            let self = this
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'Categories/' + self.currentComponent

                })
                .then(res => {
                    // console.log('vvvvvvvvv',res)
                    for (let ele in res.data.content) {
                        self.categoriesFilter.push({
                            'label': res.data.content[ele].categoryName,
                            'value': res.data.content[ele].categoryName
                        })
                        self.categories.push(res.data.content[ele].categoryName)
                    }
                    // console.log('projetcCategoriesList',self.projetcCategoriesList)
                    self.columns1[2].filters = self.categoriesFilter // 必须要设置一下 ，目录列表
                })
                .catch(err => {
                    self.$Message.error(err.response.data.detail);
                })
        },
        handleBeforeUpload: function (file) {
            // 批量上传
            let operator = localStorage.getItem('kwmUser')
            let self = this;
            let Alldata = [];
            self.$papa.parse(file, {
                delimiter: ",",
                complete: function (results) {
                    for (let ele in results.data) {
                        // console.log(results.data[ele])
                        let temp = {};
                        if (ele > 0) {
                            let rowData = JSON.stringify(results.data[ele]);
                            rowData = JSON.parse(rowData.replace(/;/g, ","));
                            //console.log(JSON.parse(rowData))
                            if (rowData[0]) {
                                temp["word"] = rowData[0];
                                temp["categories"] = eval(rowData[1]);
                                temp["operator"] = operator
                            } else {
                                continue;
                            }
                        } else {
                            continue;
                        }
                        Alldata.push(temp);
                    }
                    // 上传到后端
                    self
                        .axios({
                            method: "post",
                            url: self.baseurl + "UserDict/" + self.currentComponent,
                            data: Alldata,
                        })
                        .then((res) => {
                            if (res.data == 'item exist') {
                                self.$Message.success("文件中有已存在的停止词!");
                            } else {
                                self.currentPage = 1;
                                if (res.data.count !== "") {
                                    self.itemCount = res.data.count;
                                }
                                self.UserDictItemData = res.data.content;
                                self.$Message.success("导入成功!");
                            }
                            // console.log(res)

                            //self.formCustom = self.formCustomOrigin
                        })
                        .catch((err) => {
                            console.log(err);
                            //self.formCustom = self.formCustomOrigin
                        });
                    console.log(Alldata);
                },
            });
            return false; // 返回false 代表 不上传
        },
        handleSelectRow: function () {
            this.selectedItemList = this.$refs.table.getSelection();
            // console.log(this.selectedItemList)
        },
        fetchAllItems: function (Params = {}) {
            let self = this;
            self.loading = true
            let pageParams = {
                currentPage: self.currentPage,
                pageSize: self.pageSize,
            };
            if (Params.pageSize) {
                pageParams = Params
            }
            // console.log(pageParams);
            self
                .axios({
                    method: "get",
                    url: self.baseurl + "UserDict/" + self.currentComponent,
                    params: pageParams,
                })
                .then((res) => {
                    //console.log(res)
                    // self.currentPage = 1
                    self.loading = false
                    if (res.data.count !== "") {
                        self.itemCount = res.data.count;
                    }
                    console.log(res.data.content)
                    self.UserDictItemData = res.data.content;
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        pageChange: function (pageIndex) {
            // console.log(pageIndex)
            this.currentPage = pageIndex;
            this.fetchAllItems();
        },
        pageSizeChange: function (pageSize) {
            this.pageSize = pageSize;
            this.currentPage = 1;
            // console.log(pageSize)
            this.fetchAllItems();
        },
    },
}
</script>

<style scoped>
.my-title {
    margin-bottom: 16px;
}

.my-container {
    margin: 30px;
}

>>>.ivu-icon.ivu-icon-ios-search.ivu-input-icon.ivu-input-icon-normal.ivu-input-search-icon,
.ivu-icon.ivu-icon-ios-close-circle.ivu-input-icon.ivu-input-icon-clear.ivu-input-icon-normal {
    padding-top: 0px !important
}
</style>
